        <img v-for="cliente of clientes" :key="cliente" :src="'clientesLogos/' + cliente.image + '.png'" alt="">
        <img v-for="cliente of clientes" :key="cliente" :src="'clientesLogos/' + cliente.image + '.png'" alt="">
      </div>
    </div>
    <p>Somos gratos pela confiança e preferência de cada um dos nossos clientes. Buscamos sempre oferecer o melhor
      serviço e atendimento e suprir sempre as necessidades e dificuldades que possam surgir.
    </p>
  </section>
