<script>
import axios from 'axios';

export default {
  name: 'Clientes&ParceirosView',
  data() {
    return {
      clientes: []
    }
  },
  mounted() {
    this.getClientsLogos()
  },
  methods: {
    getClientsLogos() {
      axios.get('clientesLogos/logosList.json').then(response => {
        this.clientes = response.data
      })
    },
  }
}
</script>
